import React from 'react';
import './Main.css';
import Profile from '../Profile/Profile';
import WearCategory from '../WearCategory/WearCategory';
import { tShirtList,pantsList, shortsList, hoodieList, accessoriesList } from '../../Utils/constants';
import GenerateIcon from '../../images/icons/generateIcon.svg';
import AddIcon from '../../images/icons/add-button.svg';
function Main(props) {

    return (
        <div className='main'>
            <button onClick={props.onGenerateClothes} className='generate__button'>Generate <img className='generate__icon' src={GenerateIcon}></img></button>
            <button onClick={props.onAddClothes} className='generate__button'> <img className='add-button' src={AddIcon}></img></button>
            <WearCategory
            id='shirts'
            array={tShirtList}
            category=''
            ></WearCategory>
            <WearCategory
            id='pants'
            array={pantsList}
            category=''
            ></WearCategory>
            <WearCategory
            id='shorts'
            array={shortsList}
            category=''
            ></WearCategory>
            <WearCategory
            id='hoodie'
            array={hoodieList}
            category=''
            ></WearCategory>
            <WearCategory
            id='access'
            array={accessoriesList}
            category=''
            ></WearCategory>
        </div>
    );
}

export default Main;